.aboutBody {
    font-family: Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    line-height: 2;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    width: 80%;
    margin: 2% auto 2% auto;
}

div.polaroid {
    margin: 2% auto 2% auto;
    width: 40%;
    background-color: white;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.img {
    width: 100%;
    height: 100%;
}

.about {
    width: 50%;
}

.quickIntro {
    font-size: 2em;
    font-weight: 300;
}

.highlight {
    background-color: #E5F1F0;
    background-image: -webkit-gradient(linear, left top, right top, from(#cce2df), to(#E5F1F0));
    background-image: -webkit-linear-gradient(left, #cce2df 0%, #E5F1F0 100%);
    background-image: -o-linear-gradient(left, #cce2df 0%, #E5F1F0 100%);
    background-image: linear-gradient(to right, #cce2df 0%, #E5F1F0 100%);
}


.aboutText {
    font-size: 1.1em;
}

.buttons {
    margin: 5% 0 0 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 35%;
}

.abtButton {
    background-color: #fff;
    border: solid 1px #028090;
    border-radius: 3px;
    font-size: 1em;
    font-weight: 300;
    height: 3em;
    text-align: center;
    color: #3f4045;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    cursor: pointer;
}

.abtButton:hover {
    background-color: #E5F1F0;
}

/**
    use these styles for devices no larger than
    800px and no smaller then 320px
**/
@media (max-width: 800px) and (min-width: 320px){
    .aboutBody {
        display: block;
    }

    .about {
        width: 100%;
        margin: 5% auto 5% auto;
    }

    div.polaroid {
        margin: 4% auto 4% auto;
        width: 80%;
    }

    .buttons {
        width: 55vw;
    }
}


